import React, { Component } from 'react'
import {bitcoinService} from '../../services/bitcoinService'
import { Chart } from '../../components/Chart/Chart'

export class Statistics extends Component {
    state = {
        marketPrice: null,
        confirmedTransactions: null,
        avgBlockSize: null
    }
    componentDidMount() {
        this.loadMarketPrice()
        this.loadConfirmedTransactions()
        this.loadAvgBlockSize()
    }
    async loadMarketPrice() {
        const marketPrice = await bitcoinService.getMarketPrice()
        this.setState({ marketPrice })
    }
    async loadConfirmedTransactions() {
        const confirmedTransactions = await bitcoinService.getConfirmedTransactions()
        this.setState({ confirmedTransactions })
    }
    async loadAvgBlockSize() {
        const avgBlockSize = await bitcoinService.getAvgBlockSize()
        this.setState({ avgBlockSize })
    }
    render() {
        const { marketPrice, confirmedTransactions, avgBlockSize } = this.state
        if (!marketPrice || !confirmedTransactions || !avgBlockSize) return <div>Loading...</div>
        return (
            <div>
                <h2>Statistics</h2>
                <Chart data={ marketPrice } color={'#f9ed69'}></Chart>
                <Chart data={ confirmedTransactions } color={'#f08a5d'}></Chart>
                <Chart data={ avgBlockSize } color={'#b83b5e'}></Chart>
            </div>
        )
    }
}
