const INITIAL_STATE = {
    // user: { name: 'Shraga', balance: 100 }
    user: null
}

export function UserReducer(state = INITIAL_STATE, action) {
    // console.log('userReducer', state, action);
    switch (action.type) {
        case 'SET_USER':
            return {
                ...state,
                user: action.user
            }
        case 'SPEND_BALANCE':
            return {
                ...state,
                user: { ...state.user, balance: state.user.balance - action.spend }
            }
        default:
            return state
    }
}