import React from 'react'
import { Route, NavLink, Switch } from 'react-router-dom'
export function About(props) {
    function StaffList(props) {
        return (
            <div>
                <h2>Our Staff</h2>
                <ul>
                    <li>Puki</li>
                    <li>Popo</li>
                </ul>
            </div>
        )
    }
    const StaffVision = () => (
        <div>
            <h2>Vision</h2>
            <ol>
                <li>Eat</li>
                <li>Love</li>
                <li>Pray</li>
            </ol>
        </div>
    )
    return (
        <div>
            <h2>About</h2>
            <ul>
                <li><NavLink to="/about/staff">Staff</NavLink></li>
                <li><NavLink to="/about/staff/vision">Vision</NavLink></li>
            </ul>
            <Switch>
                <Route path="/about/staff/vision" component={ StaffVision } />
                <Route path="/about/staff" component={ StaffList } />
            </Switch>
        </div>
    )
}
