import React, { Component } from 'react';
import { connect } from 'react-redux'
import { loadUser } from './actions/userActions'
import { HashRouter as Router, Switch, Route } from 'react-router-dom'

import { Navbar } from './components/Navbar/Navbar'
import { HomePage } from './pages/HomePage/HomePage'
import { ContactApp } from './pages/ContactApp/ContactApp'
import { ContactEdit } from './pages/ContactEdit/ContactEdit'
import { ContactDetails } from './pages/ContactDetails/ContactDetails'
import { Statistics } from './pages/Statistics/Statistics'
import { About } from './pages/About/About'
import { CamApp } from './pages/Cam/CamApp'

import { Signup } from './pages/Signup/Signup'

import './App.scss';

class _App extends Component {
    componentDidMount() {
        this.props.loadUser()
    }
    render() {
        const { user } = this.props
        if (!user) return <Signup />
        return (
            <Router>
                <Navbar />
                <div className="App">
                <Switch>
                    <Route component= { ContactEdit } path="/contact/edit/:_id?" />
                    <Route component= { ContactDetails } path="/contact/:_id" />
                    <Route component= { ContactApp } path="/contact" />
                    <Route component= { Statistics } path="/stats" />
                    <Route component= { About } path="/about" />
                    <Route component= { CamApp } path="/cam" />
                    <Route component= { HomePage } exact path="/" />
                </Switch>
                </div>
            </Router>
        )
    }
}

function mapStateProps(state) {
    return {
        user: state.UserReducer.user
    }
}

const mapDispatchToProps = {
    loadUser
}

export const App = connect(mapStateProps, mapDispatchToProps)(_App)
