const axios = require("axios");

export const bitcoinService = {
    getRate,
    getMarketPrice,
    getConfirmedTransactions,
    getAvgBlockSize
};

const baseUrl = 'https://api.blockchain.info/'
const timeSpan = 'timespan=5months'
const jsonCors = 'format=json&cors=true';

async function getRate() {
    return await _fetch(`tobtc?currency=USD&value=1`)
}

async function getMarketPrice() {
    return await _fetch(`charts/market-price?${timeSpan}&${jsonCors}`)
}

async function getConfirmedTransactions() {
    return await _fetch(`charts/trade-volume?${timeSpan}&${jsonCors}`)
}

async function getAvgBlockSize() {
    return await _fetch(`charts/avg-block-size?${timeSpan}&${jsonCors}`)
}

async function _fetch(endPoint) {
    try {
        const response = await axios.get(`${baseUrl}${endPoint}`);
        // console.log(`${baseUrl}${endPoint}`);
        return response.data
    } catch (error) {
        console.error(error);
        return error
    }
}
