import React, { Component } from 'react'
import { connect } from 'react-redux'
import { setUser } from '../../actions/userActions'

class _Signup extends Component {
    state = {
        user: {
            name: ''
        }
    }
    handleChange = ( { target } ) => {
        const field = target.name
        const value = target.type === 'number' ? +target.value : target.value
        this.setState(({ user }) => ({ user: { ...user, [field]: value } }))
    }
    onSignup = async (ev) => {
        ev.preventDefault()
        this.props.setUser(this.state.user.name)
    }
    render() {
        const { user } = this.state
        return (
            <div>
                <h2>Signup PLZ</h2>
                <form onSubmit={this.onSignup}>
                    <input type="text" name="name" value= {user.name} onChange={this.handleChange} />
                    <button>Sign up</button>
                </form>
            </div>
        )
    }
}

// gets the global state and puts it in the props of the component
function mapStateProps(state) {
    return {
        contacts: state.UserReducer.user
    }
}
// Takes the action dispatchers from the actions file and puts them inside the component's props
const mapDispatchToProps = {
    setUser,
}
// Connect is used to tap into the store, without it we have no access to the store from the component
export const Signup = connect(mapStateProps, mapDispatchToProps)(_Signup)