import { contactService } from '../services/contactService'
// Action Creator
const _setContacts = (contacts) => ({ type: 'SET_CONTACTS', contacts })
const _removeContact = (id) => ({ type: 'REMOVE_CONTACT', id })

// Action Dispatcher 
export function loadContacts() {
    return async (dispatch, getState) => {
        const contacts = await contactService.getContacts(getState().filterBy)
        dispatch(_setContacts(contacts))
    }
}

export function removeContact(id) {
    return dispatch => {
        return contactService.deleteContact(id)
            .then( () => dispatch(_removeContact(id)))
    }
}

export function setFilter(filterBy) {
    return dispatch => {
        dispatch({ type: 'SET_FILTER', filterBy })
    }
}