import React, { Fragment, useRef } from 'react';
import { Camera } from 'react-cam';
import {ImgUpload} from '../../services/imgUploadService.js'

function capture(imgSrc) {
//   console.log(imgSrc);
    ImgUpload(imgSrc)
    alert('captured!')
}

export const CamApp = () => {
  const cam = useRef(null);
  const { innerWidth, innerHeight } = window
  document.body.style.zoom = "50%"

  console.log('width:', innerWidth * .9 * 2);
  return (
    <Fragment>
      <Camera
        // showFocus={true}
        front={false}
        capture={capture}
        ref={cam}
        width={innerWidth * .9 * 2}
        height={ innerHeight < innerWidth ? innerHeight * .9 * 2 : innerWidth * .9 * 2}
        focusWidth="40%"
        focusHeight="40%"
        btnColor="white"
      />
      <button onClick={img => cam.current.capture(img)}>Take image</button>
    </Fragment>
  );
};
