import React, { Component } from 'react'
import { connect } from 'react-redux'
import { bitcoinService } from '../../services/bitcoinService'

class _HomePage extends Component {
    state = {
        rate: null,
        greet: ''
    }
    componentDidMount() {
        this.loadRate()
        this.setGreet()
    }
    setGreet() {
        // const hr = 
        // console.log(hr);
        var greet = 'hi'
        // switch (hr = new Date().getHours()) {
        //     case ( hr >= 17 && hr < 21):
        //         greet = 'Good evening';
        //         break
        //     case ( hr >= 21 || hr < 5):
        //         greet = 'Good night';
        //         break
        //     case ( hr >=5 && hr < 12):
        //         greet = 'Good morning';
        //         break
        //     case ( hr > 12 && hr < 17):
        //         greet = 'Good afternoon';
        //         break
        //     // default:
        //     //     greet = 'Hi there'
        // }
        this.setState({ greet })
    }
    async loadRate() {
        const rawRate = await bitcoinService.getRate()
        const rate = (1 / rawRate).toLocaleString() //.slice(0, -1)
        this.setState({ rate })
    }
    render() {
        const { rate, greet } = this.state
        const user = this.props.user
        if ( !rate ) return <div>Loading...</div>
        return (
            <main className="HomePage">
                <h2>{greet} {user.name}</h2>
                <h3>Coins: {this.props.user.coins}</h3>
                <h3>Rate: ${rate}</h3>
            </main>
        )
    }
}
function mapStateProps(state) {
    return {
        user: state.UserReducer.user,
    }
}

export const HomePage = connect(mapStateProps)(_HomePage)