import React, { Component } from 'react'
import { contactService } from '../../services/contactService'

export class ContactDetails extends Component {
    state = {
        contact: null
    }
    async componentDidMount() {
        const { _id } = this.props.match.params
        const contact = await contactService.getContactById(_id)
        this.setState({ contact })
    }
    onEdit = (ev) => {
        const { _id } = this.props.match.params
        ev.stopPropagation()
        this.props.history.push(`/contact/edit/${_id}`)
    }
    onDeleteContact = async () => {
        await contactService.deleteContact(this.state.contact._id)
        this.props.history.push('/contact')
    }
    render() {
        if (!this.state.contact) return <div>Loading...</div>
        const { name, email, phone, _id } = this.state.contact
        return (
            <div className="contact-details">
                <img src={ `https://robohash.org/${_id}` } alt="" />
                <p>{ name }</p>
                <p>{ email }</p>
                <p>{ phone }</p>
                <button onClick={ this.onEdit }>Edit</button>
                <button onClick={ this.onDeleteContact }>Delete</button>
                <button onClick={ () => this.props.history.goBack() }>Back</button>
            </div>
        )
    }
}

