import React, { Component } from 'react'
import { contactService } from '../../services/contactService'

export class ContactEdit extends Component {
    state = {
        contact: null
    }
    async componentDidMount() {
        const { _id } = this.props.match.params
        const contact = _id ? await contactService.getContactById(_id) : await contactService.getEmptyContact()
        this.setState({ contact })
    }
    handleChange = ({ target }) => {
        const field = target.name
        const value = target.type === 'number' ? +target.value : target.value
        this.setState(({ contact }) => ({ contact: { ...contact, [field]: value } }))
    }
    onSaveRobot = async (ev) => {
        ev.preventDefault()
        await contactService.saveContact(this.state.contact)
        this.props.history.push('/contact')
    }
    onCancel = (ev) => {
        ev.preventDefault()
        this.props.history.push('/contact')
    }
    render() {
        const { contact } = this.state
        if (!contact) return <div>Loading...</div>
        return (
            <form onSubmit= { this.onSaveRobot }>
                <h2>Contact edit</h2>
                <label htmlFor="name">Name</label>
                <input id="name" type="text" name="name" value={ contact.name } onChange={ this.handleChange } />
                <br/>
                <label htmlFor="email">Email</label>
                <input id="email" type="text" name="email" value={ contact.email } onChange={ this.handleChange } />
                <br/>
                <label htmlFor="phone">Phone</label>
                <input id="phone" type="text" name="phone" value={ contact.phone } onChange={ this.handleChange } />
                <br/>
                <button>Save</button>
                <button onClick= { this.onCancel }>Cancel</button>
            </form>
        )
    }
}
